/* eslint-disable import/no-unresolved */
/* eslint-disable max-lines */
import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import DSInputMask, {
  MASK_TYPES,
  MASK_PIPES,
} from '@elliemae/ds-basic/form/InputMask';
import DSFormItemLayout from '@elliemae/ds-basic/form/FormItem';
import DSPopper from '@elliemae/ds-basic/Popper';
import { DatePickerPicker, renderMonthElement } from '@elliemae/ds-date-picker';
import DayPickerRangeController from './DayPickerRangeController';
import { isMaxDiffSafe } from '../isMaxDiffSafe';

export const START_DATE = 'startDate';
export const END_DATE = 'endDate';

const { classNameElement, classNameBlock } = convertPropToCssClassName(
  'datepicker',
);

export default class DSDateRangePickerImpl extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: props.startDate ? moment(props.startDate) : null,
      endDate: props.endDate ? moment(props.endDate) : null,
      focusedInput: START_DATE,
      showPicker: false,
      startDateError: false,
      endDateError: false,
    };
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown, false);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown, false);
  }

  onDatesChange = ({ startDate, endDate }) => {
    const { onDatesChange } = this.props;
    onDatesChange(startDate, endDate);
    const safe = isMaxDiffSafe(
      startDate,
      endDate,
      this.state.startDate,
      this.state.endDate,
    );
    if (!safe) {
      this.setState({ showPicker: false });
    }
    this.setState({
      startDate,
      endDate,
      primaryStartInput: false,
      primaryEndInput: false,
    });
  };

  onFocusChange = () => {
    this.setState(({ focusedInput }) => ({
      focusedInput: focusedInput === START_DATE ? END_DATE : START_DATE,
    }));
  };

  handleKeyDown = (event) => {
    if (event.key === 'Escape') this.setState({ showPicker: false });
  };

  getSafeStartInputValue = () => {
    const { startInput, primaryStartInput, startDate } = this.state;
    if (primaryStartInput && startInput) return startInput;
    if (!startDate) return '';
    return startDate.format('MMDDYYYY');
  };

  getSafeEndInputValue = () => {
    const { endInput, primaryEndInput, endDate } = this.state;
    if (primaryEndInput && endInput) return endInput;
    if (!endDate) return '';
    return endDate.format('MMDDYYYY');
  };

  changeFocus = () => {
    const { disabled } = this.props;
    if (!disabled) {
      this.setState(() => ({ focusedInput: 'startDate' }));
    }
  };

  handleChangeStart = (e) => {
    const { onChangeStartInput, isDayBlocked } = this.props;
    const startInputVal = e.target.value;
    let momentValue = null;
    if (startInputVal && moment(startInputVal, 'MM/DD/YYYY', true).isValid()) {
      momentValue = moment(startInputVal);
    }

    const startDateBlocked = isDayBlocked(momentValue);
    const invalidDate = startDateBlocked && startInputVal !== '';
    if (startDateBlocked) momentValue = null;

    onChangeStartInput(momentValue);
    this.setState({
      startDate: momentValue,
      startInput: startInputVal,
      primaryStartInput: true,
      startDateError: invalidDate,
    });
  };

  handleChangeEnd = (e) => {
    const { onChangeEndInput, isDayBlocked } = this.props;
    const endInputValue = e.target.value;
    let momentValue = null;
    if (endInputValue && moment(endInputValue, 'MM/DD/YYYY', true).isValid()) {
      momentValue = moment(endInputValue);
    }
    const endDateBlocked = isDayBlocked(momentValue);
    if (endDateBlocked) momentValue = null;

    const invalidDate =
      (endDateBlocked || !momentValue) && endInputValue !== '';

    onChangeEndInput(momentValue);
    this.setState({
      endDate: momentValue,
      endInput: endInputValue,
      primaryEndInput: true,
      endDateError: invalidDate,
    });
  };

  handleChangeDate = ({ startDate: _start, endDate }) => {
    if (_start && endDate) {
      this.setState({
        startDate: moment(_start),
        endDate,
        primaryStartInput: false,
        primaryEndInput: false,
      });
    } else if (_start) {
      this.setState({ startDate: moment(_start), primaryStartInput: false });
    } else if (endDate) {
      this.setState({ endDate, primaryEndInput: false });
    }
  };

  render() {
    const {
      startDate,
      endDate,
      focusedInput,
      showPicker,
      startDateError,
      endDateError,
    } = this.state;

    const {
      disabled,
      displayFormatDay,
      minimumNights,
      labelFrom,
      labelTo,
      isDayBlocked,
      isDayHighlighted,
      isOutsideRange,
      enableOutsideDays,
      startDateRequired,
      endDateRequired,
      onOutsideClick,
      onNextMonthClick,
      onPrevMonthClick,
      onClose,
      hasError,
      validationMessage,
    } = this.props;

    const inputClass = classNameBlock('inputs', 'range-picker');
    const inputClassModifier = '';

    const safeStartInputValue = this.getSafeStartInputValue();
    const safeEndInputValue = this.getSafeEndInputValue();
    return (
      <div className={`${inputClass} ${inputClassModifier}`}>
        <div
          className={classNameBlock(
            'wrapper-input-date-range-picker-controller',
          )}
        >
          <DSFormItemLayout
            floatingLabel
            inputComponent={DSInputMask}
            labelText={labelFrom}
            mask={MASK_TYPES.DATE}
            onChange={this.handleChangeStart}
            onFocus={() => this.setState({ showPicker: false })}
            pipe={MASK_PIPES.AUTO_CORRECT_DATE}
            value={safeStartInputValue}
            required={startDateRequired}
            hasError={hasError || startDateError}
            placeholder="MM/DD/YYYY"
          />
          <DSFormItemLayout
            floatingLabel
            inputComponent={DSInputMask}
            labelText={labelTo}
            mask={MASK_TYPES.DATE}
            onChange={this.handleChangeEnd}
            onFocus={() => this.setState({ showPicker: false })}
            pipe={MASK_PIPES.AUTO_CORRECT_DATE}
            value={safeEndInputValue}
            required={endDateRequired}
            hasError={hasError || endDateError}
            validationMessage={validationMessage}
            placeholder="MM/DD/YYYY"
          />
        </div>
        <div
          className={classNameBlock(
            'wrapper-button-date-range-picker-controller',
          )}
        >
          <DSPopper
            contentComponent={
              <div>
                {showPicker && (
                  <DayPickerRangeController
                    enableOutsideDays={enableOutsideDays}
                    isDayBlocked={isDayBlocked}
                    isDayHighlighted={isDayHighlighted}
                    isOutsideRange={isOutsideRange}
                    displayFormatDay={displayFormatDay}
                    endDate={endDate}
                    focusedInput={focusedInput}
                    hideKeyboardShortcutsPanel
                    minimumNights={minimumNights}
                    onDatesChange={this.onDatesChange}
                    onFocusChange={this.onFocusChange}
                    onOutsideClick={(e) => {
                      onOutsideClick(e);
                      this.setState({ showPicker: false });
                    }}
                    onNextMonthClick={onNextMonthClick}
                    onPrevMonthClick={onPrevMonthClick}
                    startDate={startDate}
                    renderMonthElement={renderMonthElement}
                  />
                )}
              </div>
            }
            isOpen={showPicker && !startDateError && !endDateError}
            onClose={onClose}
            placement="bottom-end"
            showArrow={false}
            triggerComponent={
              <div onFocus={() => this.setState({ showPicker: true })}>
                <DatePickerPicker
                  className={classNameElement('picker')}
                  disabled={disabled}
                  onClick={() => this.setState({ showPicker: true })}
                />
                {hasError && validationMessage && (
                  <div style={{ height: 15 }} />
                )}
              </div>
            }
          />
        </div>
      </div>
    );
  }
}

DSDateRangePickerImpl.propTypes = {
  startDate: PropTypes.instanceOf(Date, moment),
  endDate: PropTypes.instanceOf(Date, moment),
  onDatesChange: PropTypes.func,
  onChangeStartInput: PropTypes.func,
  onChangeEndInput: PropTypes.func,
  onNextMonthClick: PropTypes.func,
  onPrevMonthClick: PropTypes.func,
  onClose: PropTypes.func,
  onOutsideClick: PropTypes.func,
  disabled: PropTypes.bool,
  displayFormatDay: PropTypes.string,
  minimumNights: PropTypes.number,
  labelTo: PropTypes.string,
  labelFrom: PropTypes.string,
  isDayBlocked: PropTypes.func,
  isDayHighlighted: PropTypes.func,
  isOutsideRange: PropTypes.func,
  enableOutsideDays: PropTypes.bool,
  startDateRequired: PropTypes.bool,
  endDateRequired: PropTypes.bool,
  hasError: PropTypes.bool,
  validationMessage: PropTypes.string,
};
