/* eslint-disable max-lines */
import React from 'react';
import { PropTypes, describe } from 'react-desc';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import moment from 'moment';
import 'react-dates/initialize';
import { DateRangePickerPhrases } from 'react-dates';
import DayPickerRangeController from './components/DayPickerRangeController';
import DSDateRangePickerImpl, {
  START_DATE,
  END_DATE,
} from './components/DSDateRangePickerImpl';

const DSDateRangePicker = ({
  containerProps = {},
  className = '',
  disabled = false,
  required = false,
  startDateRequired = false,
  endDateRequired = false,
  id,
  hasError = false,
  validationMessage = '',
  readOnly = false,
  screenReaderInputMessage = 'Select date',
  initialVisibleMonth = moment(),
  firstDayOfWeek = 0,
  keepOpenOnDateSelect = true,
  reopenPickerOnClearDates = false,
  hideKeyboardShortcutsPanel = true,
  onPrevMonthClick = () => null,
  onNextMonthClick = () => null,
  onClose = () => null,
  onOutsideClick = () => null,
  transitionDuration = 0,
  verticalSpacing = 0,
  minimumNights = 0,
  isDayBlocked = () => false,
  isOutsideRange = () => false,
  isDayHighlighted = () => false,
  displayFormat = 'L',
  displayFormatDay = 'D',
  phrases = DateRangePickerPhrases,
  startDate,
  endDate,
  startDateId = 'startDateId',
  endDateId = 'endDateId',
  labelFrom = 'From',
  labelTo = 'To',
  enableOutsideDays = true,
  onChangeStartInput = () => null,
  onChangeEndInput = () => null,
  onDatesChange = () => null,
}) => {
  const { cssClassName, prefixBlockName } = convertPropToCssClassName(
    'datepicker',
    className,
    {
      hasError,
      readOnly,
      disabled,
      id,
    },
  );
  return (
    <div
      {...containerProps}
      className={`${prefixBlockName}-range ${cssClassName} ${prefixBlockName}-range`}
    >
      <DSDateRangePickerImpl
        appendToBody
        disabled={disabled}
        displayFormat={displayFormat}
        displayFormatDay={displayFormatDay}
        enableOutsideDays={enableOutsideDays}
        endDate={endDate}
        endDateId={endDateId}
        firstDayOfWeek={firstDayOfWeek}
        hideKeyboardShortcutsPanel={hideKeyboardShortcutsPanel}
        initialVisibleMonth={() =>
          initialVisibleMonth ? moment(initialVisibleMonth) : moment(startDate)
        }
        isDayBlocked={isDayBlocked}
        isDayHighlighted={isDayHighlighted}
        isOutsideRange={
          isOutsideRange === true || isOutsideRange === false
            ? () => isOutsideRange
            : isOutsideRange
        }
        keepOpenOnDateSelect={keepOpenOnDateSelect}
        labelFrom={labelFrom}
        labelTo={labelTo}
        minimumNights={minimumNights}
        onDatesChange={onDatesChange}
        onChangeStartInput={onChangeStartInput}
        onChangeEndInput={onChangeEndInput}
        onClose={onClose}
        onNextMonthClick={onNextMonthClick}
        onPrevMonthClick={onPrevMonthClick}
        onOutsideClick={onOutsideClick}
        phrases={phrases}
        readOnly={readOnly}
        reopenPickerOnClearDates={reopenPickerOnClearDates}
        required={required}
        startDateRequired={startDateRequired}
        endDateRequired={endDateRequired}
        screenReaderInputMessage={screenReaderInputMessage}
        startDate={startDate}
        startDateId={startDateId}
        transitionDuration={transitionDuration}
        verticalSpacing={verticalSpacing}
        hasError={hasError}
        validationMessage={validationMessage}
      />
    </div>
  );
};

const dateRangePickerProps = {
  containerProps: PropTypes.shape({}).description(
    'Set of Properties attached to the main container',
  ),
  className: PropTypes.string.description('html class attribute'),
  disabled: PropTypes.bool
    .description('disable component or not')
    .defaultValue(false),
  required: PropTypes.bool
    .description('input is required or not')
    .defaultValue(false),
  startDateRequired: PropTypes.bool
    .description('start date is required or not')
    .defaultValue(false),
  endDateRequired: PropTypes.bool
    .description('end date is required or not')
    .defaultValue(false),
  id: PropTypes.string.description('components id'),
  hasError: PropTypes.bool.description('set error state').defaultValue(false),
  validationMessage: PropTypes.string.description('validation message'),
  readOnly: PropTypes.bool
    .description('component is read only or not')
    .description(false),
  screenReaderInputMessage: PropTypes.string
    .description('message to be anounced by screen reader')
    .defaultValue('select date'),
  initialVisibleMonth: PropTypes.instanceOf(Date, moment).description(
    'initial visible month',
  ),
  firstDayOfWeek: PropTypes.number
    .description('first day of week')
    .defaultValue(0),
  keepOpenOnDateSelect: PropTypes.bool
    .description('keep picker open when a date is selected')
    .defaultValue(true),
  reopenPickerOnClearDates: PropTypes.bool
    .description('reopen picker when clearing dates')
    .defaultValue(false),
  hideKeyboardShortcutsPanel: PropTypes.bool.description(
    'hide keyabord shortcut panel or not',
  ),
  onPrevMonthClick: PropTypes.func.description(
    'function executed when clicking on prev month button',
  ),
  onNextMonthClick: PropTypes.func.description(
    'function executed when clicking on next month button',
  ),
  onClose: PropTypes.func.description(
    'function executed when component closes',
  ),
  transitionDuration: PropTypes.number
    .description('transition duration')
    .defaultValue(0),
  verticalSpacing: PropTypes.number.description('vertical spacing'),
  isDayBlocked: PropTypes.func.description(
    'function that returns each day and disables desired dates',
  ),
  isOutsideRange: PropTypes.func.description(
    'function that returns each day and disables outisde range dates',
  ),
  isDayHighlighted: PropTypes.func.description(
    'function that returns each day and higlights desired dates',
  ),
  minimumNights: 0,
  displayFormat: PropTypes.string
    .description('display format')
    .defaultValue('L'),
  displayFormatDay: PropTypes.string
    .description('display format day')
    .defaultValue('D'),
  phrases: PropTypes.shape({
    calendarLabel: PropTypes.string,
    closeDatePicker: PropTypes.string,
    clearDates: PropTypes.string,
    focusStartDate: PropTypes.string,
    jumpToPrevMonth: PropTypes.string,
    jumpToNextMonth: PropTypes.string,
    keyboardShortcuts: PropTypes.string,
    showKeyboardShortcutsPanel: PropTypes.string,
    hideKeyboardShortcutsPanel: PropTypes.string,
    openThisPanel: PropTypes.string,
    enterKey: PropTypes.string,
    leftArrowRightArrow: PropTypes.string,
    upArrowDownArrow: PropTypes.string,
    pageUpPageDown: PropTypes.string,
    homeEnd: PropTypes.string,
    escape: PropTypes.string,
    questionMark: PropTypes.string,
    selectFocusedDate: PropTypes.string,
    moveFocusByOneDay: PropTypes.string,
    moveFocusByOneWeek: PropTypes.string,
    moveFocusByOneMonth: PropTypes.string,
    moveFocustoStartAndEndOfWeek: PropTypes.string,
    returnFocusToInput: PropTypes.string,
    keyboardNavigationInstructions: PropTypes.string,
    chooseAvailableStartDate: PropTypes.func,
    chooseAvailableEndDate: PropTypes.func,
    dateIsUnavailable: PropTypes.func,
    dateIsSelected: PropTypes.func,
  }).description('react-dates phrases'),
  startDate: PropTypes.instanceOf(Date, moment).description('start date value'),
  endDate: PropTypes.instanceOf(Date, moment).description('end date value'),
  startDateId: PropTypes.string.description('start date id'),
  endDateId: PropTypes.string.description('end date id'),
  labelFrom: PropTypes.string.description('label of start date input'),
  labelTo: PropTypes.string.description('label of end date input'),
  enableOutsideDays: PropTypes.bool
    .description('enable days outside current month')
    .defaultValue('true'),
  onDatesChange: PropTypes.func.description(
    'function executed when dates change',
  ),
  onChangeStartInput: PropTypes.func.description(
    'function executed when start date input changes',
  ),
  onChangeEndInput: PropTypes.func.description(
    'function executed when end date input changes',
  ),
  onOutsideClick: PropTypes.func.description(
    'function executed when clicking outside component',
  ),
};

DSDateRangePicker.propTypes = dateRangePickerProps;
const DateRangePickerWithSchema = describe(DSDateRangePicker);
DateRangePickerWithSchema.propTypes = dateRangePickerProps;

export {
  DayPickerRangeController,
  START_DATE,
  END_DATE,
  DateRangePickerWithSchema,
};

export default DSDateRangePicker;
