/* eslint-disable max-params */
export const MAX_YEARS_DIFF = 150;
// https://jira.elliemae.io/browse/PUI-4145
export const isMaxDiffSafe = (startDate, endDate, prevStart, prevEnd) => {
  try {
    const diff = Math.abs(endDate.diff(startDate, 'years'));
    const diffStart = Math.abs(startDate.diff(prevStart, 'years'));
    const diffEnd = Math.abs(endDate.diff(prevEnd, 'years'));
    return (
      diff < MAX_YEARS_DIFF &&
      diffStart < MAX_YEARS_DIFF &&
      diffEnd < MAX_YEARS_DIFF
    );
  } catch (error) {
    return true;
  }
};
